@import url(https://fonts.googleapis.com/css2?family=Pangolin&display=swap);
:root {
  --content-background-color: #fff6be;
  --background-image: url(/tic-tac-toe/static/media/blackboard3.5bb6b59a.jpg);

  --primary-color: #898c8f;
  --primary-brighter: #7d7d7d;
  --primary-saturated: #696969;
  --primary-saturated-brighter: #6e6a6a;
  --accent-color: beige;
  --accent-darker: #a79000;
  /* --accent-darker: #a79000; */
  --box-shadow-color: rgba(0, 0, 0, 0.75);
  --modal-overlay-color: rgba(0, 0, 0, 0.3);
  --primary-gradient: linear-gradient(
    to right,
    var(--primary-color) 0%,
    var(--primary-saturated) 100%
  );
  --primary-highlight-gradient: linear-gradient(
    to right,
    var(--primary-brighter) 0%,
    var(--primary-saturated-brighter) 100%
  );
  --box-shadow: 0 1vmin 2vmin -1vmin rgba(0, 0, 0, 0.75);
  --font-texture: url(/tic-tac-toe/static/media/texture.a1bc7305.jpg);
  --image-border: url(/tic-tac-toe/static/media/border-1.78040c5e.png);
  --default-border-radius: 2vmin;
  --default-primary-border: 0.25vmin solid var(--primary-color);
  --default-accent-border: 0.25vmin solid var(--accent-darker);
  --main-title-font-size: 7vmin;
  --secondary-title-font-size: 4.5vmin;

  --selection-animation-duration: 300ms;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  width: 100%;
}

html,
body {
  height: 100%;
  /* display: grid;
  place-items: center center; */
  overflow: hidden;
}

body {
  display: grid;
  place-items: center center;
  background: url(/tic-tac-toe/static/media/blackboard3.5bb6b59a.jpg);
  background: var(--background-image);

  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  font-family: "Pangolin", cursive;
}

#game {
  border-radius: var(--default-border-radius);
  padding: 2vmin;
}

h1 {
  color: var(--accent-color);
  text-align: center;
  margin-bottom: 1vmin;
  font-size: var(--main-title-font-size);
  background: var(--font-texture) no-repeat center center;
  background-size: cover;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
}

h3,
p {
  color: var(--accent-color);
  font-size: var(--secondary-title-font-size);
  background: var(--font-texture) no-repeat center center;
  background-size: cover;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
}

/* playerinfo */
form {
  margin: auto;
  padding: 0;
  width: 100vmin;
  box-sizing: border-box;
  text-align: center;
}

input {
  width: 80%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: none;
  border-bottom: 2px solid #ccc;
  border-image: var(--image-border) 30 round;
  background-color: transparent;
  /* border-radius: 4px; */
  box-sizing: border-box;
  color: var(--accent-color);
  font-family: "Pangolin", cursive;
  font-size: 3.5vmin;
}

.input:focus {
  outline: none !important;
  border: 1px solid papayawhip;
  box-shadow: 0 0 5px #719ece;
}
button {
  width: 100%;
  background-color: var(--primary-saturated);
  /* background-color: var(--primary-gradient); */
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-family: "Pangolin", cursive;
  font-size: 3.5vmin;
}
button:hover {
  /* background-color: var(--primary-brighter); */
  background: var(--primary-highlight-gradient);
}

/* Create three equal columns that floats next to each other */
.column {
  float: left;
  width: 33%;
  padding: 5px;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

.fas:hover {
  cursor: pointer;
}
.icon-column {
  float: right;
  width: 20%;
}

/* Checkbox */
:root {
  font-size: 3.5vmin;
}

input[type="checkbox"] {
  display: none;
}

input + label {
  width: 15vmin;
  /* height: 1.5rem; */
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 13px 20px;
  margin: 8px 0;
  border-radius: 4px;
  background-color: var(--primary-saturated);
  transition: 0.1s all;
  cursor: pointer;
  z-index: 999 !important;
}

input + label:hover {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
  background: var(--primary-highlight-gradient);
}

input + label:after {
  content: "üë®‚Äçüë¶";
  color: #ccc;
}

input:checked + label {
  background: var(--primary-saturated);
}

input:checked + label:hover {
  background: var(--primary-brighter);
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

input:checked + label:after {
  content: "üñ•Ô∏è";
}
/* Checkbox end*/

.row-h {
  width: 80%;
  display: inline-block;
}

.col-2 {
  width: 20%;
  float: left;
}
.col-12 {
  width: 80%;
  float: right;
}

#board {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(3, 20vmin);
  grid-template-rows: repeat(3, 20vmin);
  grid-gap: 0;
  margin: 2vmin 0;
}

/* @import url("https://fonts.googleapis.com/css2?family=Pangolin&display=swap");
body {
  font-family: "Pangolin", cursive;
} */
.cell {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 10vmin;
  font-weight: bold;
  color: var(--accent-color);
  /* background: var(--primary-gradient); */
  /* border: var(--default-primary-border); */
  /* border-radius: var(--default-border-radius); */
  border: 2vmin solid white;
  border-image: var(--image-border) 50 round;
  /* outline: none; */
  /* box-shadow: var(--box-shadow); */
  font-family: "Pangolin", cursive;

  background: var(--font-texture) no-repeat center center;
  background-size: cover;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
}

.cell:active,
.cell:hover,
.cell.winner {
  cursor: pointer;
  color: var(--content-background-color);

  background: var(--font-texture) no-repeat center center;
  background-size: cover;
  -webkit-text-fill-color: var(--content-background-color);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
}

.cell-content {
  /* overflow: hidden; */
  width: 0;
  height: 0;
  transition: width var(--selection-animation-duration),
    height var(--selection-animation-duration);
}

.cell-content.populated {
  width: 12vmin;
  height: 12vmin;
}

.cell.tie,
.cell-content.winner {
  -webkit-animation: blinker 1s linear 3;
          animation: blinker 1s linear 3;
}
@-webkit-keyframes blinker {
  50% {
    opacity: 0;
  }
}
@keyframes blinker {
  50% {
    opacity: 0;
  }
}

.cell-button {
  background-color: transparent;
  margin: 0%;
}

.cell-button:hover {
  background-color: #a5a5a5;
  opacity: 0.9;
}

.br_0 {
  border-right: 0;
}

.bl_0 {
  border-left: 0;
}

.bt_0 {
  border-top: 0;
}

.bb_0 {
  border-bottom: 0;
}

#modal-overlay {
  display: none;
  justify-content: center;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: var(--modal-overlay-color);
}

#modal-overlay.modal-open {
  display: grid;
}

#game-result-modal {
  display: grid;
  grid-row-gap: 75vmin;

  --modal-items-border-radius: 1vmin;
}

#result-container {
  display: flex;
  align-items: flex-end;
}

#winner-container {
  margin-bottom: 4vmin;
  width: 100%;
  height: 8vmin;
  font-size: 7vmin;
  font-weight: bold;
  /* color: var(--primary-color); */
  color: aliceblue;
  background: var(--background-image);
  /* border: var(--default-accent-border); */
  box-shadow: var(--box-shadow);
  border-radius: var(--modal-items-border-radius);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4vmin;
}

#new-game-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 2.5vmin;
}

#new-game-container > button {
  font-size: 3vmin;
  padding: 1.5vmin;
  width: auto;
  color: var(--accent-color);
  background: var(--primary-gradient);
  outline: none;
  box-shadow: var(--box-shadow);
}

#new-game-container > button:hover,
#new-game-container > button:active {
  color: var(--content-background-color);
  cursor: pointer;
  background: var(--primary-highlight-gradient);
}

img {
  width: auto;
  height: 100%;
}

